<template>
    <div id="resume">
        <h1>{{ resume.name }}</h1>
        <div class="contact">
            <p><strong>Location:</strong> {{ resume.contact.location }}</p>
            <p><strong>Email:</strong> {{ resume.contact.email }}</p>
            <p><strong>Phone:</strong> {{ resume.contact.phone }}</p>
            <p><strong>Website:</strong> <a :href="'https://' + resume.contact.website" target="_blank">{{
                resume.contact.website }}</a></p>
            <p><strong>GitHub:</strong> <a :href="'https://github.com/' + resume.contact.github" target="_blank">{{
                resume.contact.github }}</a></p>
            <p><strong>LinkedIn:</strong> {{ resume.contact.linkedin }}</p>
        </div>

        <section v-for="section in resume.sections" :key="section.title">
            <h2>{{ section.title }}</h2>

            <!-- Introduction or Quick Guide content -->
            <div v-if="section.title === 'Introduction' || section.title === 'Quick Guide'">
                <p v-if="typeof section.content === 'string'">{{ section.content }}</p>
                <ul v-else>
                    <li v-for="item in section.content" :key="item">{{ item }}</li>
                </ul>
            </div>

            <!-- Education Section -->
            <div v-if="section.title === 'Education'">
                <p><strong>{{ section.institution }}</strong></p>
                <p>{{ section.degree }} - {{ section.period }}</p>
                <p><strong>GPA:</strong> {{ section.details.GPA }}</p>
                <p><strong>Coursework:</strong> {{ section.details.coursework }}</p>
            </div>

            <!-- Experience Section -->
            <div v-if="section.title === 'Experience'">
                <div v-for="position in section.positions" :key="position.company">
                    <p><strong>{{ position.role }}</strong> at <strong>{{ position.company }}</strong> - {{
                        position.location }} ({{ position.period }})</p>
                    <ul>
                        <li v-for="detail in position.details" :key="detail">{{ detail }}</li>
                    </ul>
                </div>
            </div>

            <!-- Publications Section -->
            <div v-if="section.title === 'Publications'">
                <div v-for="publication in section.publications" :key="publication.title">
                    <p><strong>{{ publication.title }}</strong></p>
